*{
  color: #fff;
  font-family: "Cal Sans";

}
h1{
  font-weight: 400;
}
body{
  background-color: #0b111b;
  height: 100vh;
  width: 99vw;
}
.card{
  background: linear-gradient(135deg, #0f0c29, #302b63, #24243e);
  color: #00ffcc;
  font-family: 'Orbitron', sans-serif;
  text-align: center;
  border-radius: 15px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.5);
  padding: 20px;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  border: #506a9b 1px solid;
  margin: 24px;
}
.card:hover{
        transform: "scale(1.05)";
        box-shadow: "0 8px 40px rgba(0, 0, 0, 0.7)";
}
.profile-circular{
  border-radius: 200px;
  width: 200px;
  height: 200px;
  object-fit: cover;
  border: 3px solid #00ffcc;
  margin-bottom: 15px;
  transition: transform 0.8s ease-in-out;
}
.profile-circular:hover{
   transform: rotate(360deg);
}

.padded-content{
  padding: 20px;
}
.thin-text{
  font-weight: 200;
}
.medium-text{
  font-weight: 400;
}
.montserrat{
  font-family: "Montserrat";
}
#info {
  position: absolute;
  top: 10px;
  width: 100%;
  text-align: center;
  z-index: 100;
  display:block;
}
.button{
  background-color: #141e30;
  border: 1px solid #506a9b;
  color: #fff;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 12px;
  
  background: #00ffcc;
  color: #0f0c29;
  border: none;
  padding: 10px 20px;
        border-radius: 5px;
        cursor: pointer;
        font-size: 1rem;
        transition: background 0.3s, box-shadow 0.3s;
      }
.button:hover{
  
        background: #00bfa5;
        color: #ffffff;
        box-shadow: 0 4px 15px rgba(0, 191, 165, 0.5)}
.App {
  margin: 12px;
  text-align: center;
  margin: 12px;
}
.navbar{
  height: 50px;
  display: flex;
  background-color: #141e30;
  justify-content: space-between;
  align-items: center;
}
.nav-item{
  background-color:#212942;
  height: 100%;
  display: flex;
  align-items: center;
}
.nav-item>button{
  height: 100%;
  padding: 0% 28px;
  border: none;
  background-color: transparent;
  transition: 100ms;
  cursor: pointer;
}
.nav-item>button:hover{
transition: 600ms;
border-bottom: 4px solid yellow;
}
.carousel{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80vw;
  overflow-x: scroll;
  margin: auto;
}
.carousel::-webkit-scrollbar {
    display: none;
}
.carousel-card{
  background-color: #212942;
  color: #00ffcc;
  font-family: 'Orbitron', sans-serif;
  text-align: center;
  width: 800px;
  height: 400px;
  border-radius: 15px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.5);
  padding: 20px;
  transition: 400ms;
  margin: 0% 24px ;
  border: #506a9b 1px solid;
}
.carousel-card:hover{
  box-shadow: 0 8px 40px rgba(46, 144, 157, 0.7);
}
.carousel-card-date{
  font-family: "montserrat";
  font-size: 0.8rem;
  color: #00ffcc;
  margin-bottom: 10px;
}
.carousel-card-title{
  font-family: "montserrat";
  font-size: 1.4rem;
  font-weight: 400;
  color: #ffffff;
}
.carousel-card-content{
  font-family: "montserrat";
  font-size: 0.9rem;
  font-weight: 300;
  color: #ffffff;
}
.carousel-card-emoji{
  font-size: 7rem;
  margin-top: 0%;
}
.project-card{
  background-color: #212942;
  color: #00ffcc;
  font-family: 'Orbitron', sans-serif;
  text-align: center;
  width: 400px;
  height: 400px;
  border-radius: 15px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.5);
  padding: 20px;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  margin: 4% auto;
}
.project-card-mobile{
  display: flex;
  background-color: #212942;
  color: #00ffcc;
  font-family: 'Orbitron', sans-serif;
  text-align: center;
  width: 30vw;
  height: 450px;
  border-radius: 15px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.5);
  overflow: hidden;
  margin: 0.8% 2%;
  transition: 400ms;
}
.project-card-mobile:hover{
  transform: scale(105%);
  transition: 400ms;
  border: 1px solid white;
  box-shadow:0px 0px 10px 5px  #00bfa5;
}
.grid{
  display: grid !important;
  grid-template-columns: auto auto auto;
  height: 100vh;
}
.project-image-mobile{
  width: 40%;
  height: 100%;
  border-radius: 2px;
  object-fit: cover;
  transition: 600ms;
  opacity: 0;
}
.w-100{
  width: 100%;
}
.column{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
.project-image{
  width: 100%;
  height: 200px;
  border-radius: 15px;
  object-fit: cover;
}
.mouse-pointer{
  position: absolute;
  z-index: 100;
  background-color: #00ffcc;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  content: "";
}
.project-title{
  width: 100%;
  /* background-image: linear-gradient(to right, rgb(179, 0, 211), rgba(131, 1, 154, 0.435)); */
}
/* Slideshow container */
.slideshow-container {
  max-width: 1000px;
  position: relative;
  margin: auto;
}
.project-description{
  word-break: keep-all;
  width: 80%;
}
.project-link{
  border-radius: 50px;
  background-color: #00ffcc;
}
.technologies{
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
}
.buy-project-link{
  border-radius: 50px;
}
/* Hide the images by default */
.mySlides {
  display: none;
}
.project-card-mobile-content{
  width: 100%;
  background-image: linear-gradient(#12121275,#02020277);
  backdrop-filter:  contrast(-1);
  justify-content: space-evenly;
  transition: 400ms;
}
.icon{
  border-radius: 50%;
  border: 1px solid #3d4948;
  padding: 2px;
  width: 50px;
  height: 50px;
  object-fit: contain;
  margin: 0% 12px;
}

/* Next & previous buttons */
.prev, .next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
}

/* Position the "next button" to the right */
.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}

/* On hover, add a black background color with a little bit see-through */
.prev:hover, .next:hover {
  background-color: rgba(0,0,0,0.8);
}

/* Caption text */
.text {
  color: #f2f2f2;
  font-size: 15px;
  padding: 8px 12px;
  position: absolute;
  bottom: 8px;
  width: 100%;
  text-align: center;
}




/* Fading animation */
.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

@keyframes fade {
  from {opacity: .0}
  to {opacity: 1}
}